.align-btn-center {
  margin: 15px 0 15px 0;
  width: 10%;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.span {
  margin: 10px auto 0 auto;
}

.order-page__total-price {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 50%;
  margin: 0 auto 0 auto;
  background-color: rgba(39, 41, 35, 1);
  border-radius: 10px;
}
