.header {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.wrapper {
  display: flex;
  flex-grow: 1;
}

.header__store-title {
  color: rgb(83, 95, 81);
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
  transition: 0.3s;
  padding: 15px;
}
.header__store-title:hover {
  color: rgb(249, 167, 60);
  transform: translateY(-5px);
  text-shadow: #2c2828 5px 5px 0;
}
.header__store-title:active {
  color: tomato;
  transform: translateY(5px);
  text-shadow: #2c2828 2px 2px 0;
}

.header__cart-btn-wrapper {
  justify-content: flex-end;
}

.active {
  color: rgb(215, 137, 34);
}

.text-align-center {
  display: flex;
  justify-content: center;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(5px);
}
.popup {
  border-radius: 15px;
  position: relative;
  z-index: 4;
  margin: auto;
  margin-top: 5%;
  padding: auto;
  width: 60%;
  height: 66%;
  background-color: rgb(32, 28, 28);
}
